import { Button } from './styled';
import NewsPopup from '../../../common/NewsPopup';
import { AnimationType, OutAnimationType, usePopup } from 'react-custom-popup';
import { useTranslation } from '../../../core/useTranslation';

const InfoButton = () => {
  const { showModal } = usePopup();
  const translation = useTranslation();

  return (
    <Button
      onClick={() =>
        showModal(
          <NewsPopup
            author="Micha Badyga"
            content="Najwicej czasu zajo mi obmylanie wygldu designu caej aplikacji. Due lepiej korzysta z gotowego projektu od designera i nie zastanawia si nad kolorami i ukadem komponentw :) 
          Najciekawsze w tej pracy byo zrobienie zmiany wersji jzykowej aplikacji."
            extraContent="Gdybym jeszcze raz mia napisa t aplikacj, to zamiast sagi uy bym TanStack Query v4. Byo by to i prostsze i nowoczeniejsze rozwizanie 
          Dodatkowo aplikacj rozbudowa bym o:
          - przewijanie stron (paginacj).
          - wyszukiwark newsw (jest taki endpoint w API, wic mona go wykorzysta :)
          - W local storage mona byo by przechowywa (oprcz jzyka aplikacji) wybrany ostatnio ukad (kafelki/lista)."
          />,
          {
            animationType: AnimationType.SLIDE_IN_UP,
            outAnimationType: OutAnimationType.SLIDE_OUT_UP,
          }
        )
      }
    >
      {translation.header.infoButton}
    </Button>
  );
};

export default InfoButton;
